.footer-container
{
    height: 17.8%;
    overflow: hidden;
    width: 100%;
    display: flex;
    justify-content: center;
}

.footer-parent
{
    width: 100%;
    height: 100%;
    max-width: 1920px;
}

.footer-parent img
{
    max-width: 100%;
    height: 130px;

}

@media only screen and (max-width: 111) {
   .footer-parent
   {
    display: flex;
    align-items: flex-end;
   } 
}
