.profile-container
{
    display: flex;
    align-items: center;
    text-align: center;
    height: 10%;
    min-height: fit-content;
    justify-content: center;
}

.profile-parent
{
    display: flex;
    color: aliceblue;
    align-items: center;
}

.profile-details-name
{
    font-size: 24px;
    font-family: "Poppins SemiBold";
}

.profile-details-role
{
    display: flex;
    flex-direction: column;
    margin: 14px 0px 24px 0px;
}

.profile-details-role h1
{
    font-size: 40px;
    font-family: cursive;
}

.primary-text
{
    color: (--white);
}

.highlighted-text
{
    color: (--dark-orange);
}

.profile-role-tagline
{
    font-size: 19px;
    font-family: "Poppins Light";
    margin: 5px 0 0 0 ;
}

.profile-options .highlighted-btn
{
    margin:  0 0 0 28px;
}

.profile-options .highlighted-btn:hover
{
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;
}

.profile-picture
{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 0 0 var(--white);
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}


.profile-picture-background
{
    height: 93%;
    width: 93%;
    background-image: url(../../assets/Home/profilephoto.jpg);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;
}

.profile-picture-background:hover
{
    transform: scale(1.07);
    transition: 0.3s ease-out;
}

.colz .colz-icon i
{
    color: white;
    padding: 8px;
    padding-left: 0px;
}

.colz .colz-icon i:hover
{
    transform: scale(1.5);
}


@media screen and (max-width: 1110px) {
    .profile-picture
    {
        margin: 0 0px 0 80px;
    }
    .profile-details-name
    {
        font-size: 20px;
    }

    .profile-details-role .primary-text
    {
        font-size: 12px;
    }
    .profile-options .btn
    {
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }

}


@media screen and (max-width:910px){
    .profile-picture{
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
    
}

@media screen and (max-width: 810px) {
    .profile-picture{
        margin: 0 !important;
        
    }

    .home-container
    {
        padding-top: 70px;
    }

    .profile-parent
    {
        flex-direction: column-reverse;
        margin: 0px 0 0 0;
    }

    .profile-options
    {
        display: flex;
        justify-content: space-around;
    }

    .profile-details 
    {
        margin: 25px;
    }

    .profile-options .highlighted-btn
    {
        margin: 0;
    }

    .profile-options, .profile-details-role
    {
        text-align: center;
    }
}


@media screen and (max-width: 375px) {
    .profile-picture{
        height: 270px;
        width: 270px;
        margin-top: -90;
    }

    .profile-details-role h1
    {
        font-size: 30px;
    }

    .profile-role-tagline
    {
        font-size: 13px;
    }

    .profile-details
    {
        width: 90%;
    }


}